* {
  box-sizing: border-box;
}

body {
  background-color: #b5f5f1;
}

.search-form {
  width: 100%;
  min-height: 70px;
  background-color: #47e6db;
  background-image: linear-gradient(#b5f5f1, #05b3a8);
  justify-content: center;
  padding-top: 15px;
  padding-bottom: 15px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 4px 4px rgba(0, 0, 1, .5);
}

.form-input {
  text-align: center;
  color: #0c524e;
  background: #fff;
  border: 2px solid #05b3a8;
  border-radius: 6px;
  box-shadow: 0 4px 4px rgba(0, 0, 1, .5);
}

.form-input:focus {
  outline: none;
}

.form-btn, .load-more {
  text-align: center;
  width: 80px;
  color: #fff;
  cursor: pointer;
  background: #05b3a8;
  border: none;
  border-radius: 6px;
  margin-left: 15px;
  box-shadow: 0 4px 4px rgba(0, 0, 1, .5);
}

.form-btn:focus, .form-btn:hover, .load-more:focus, .load-more:hover {
  background: #127a75;
}

